/* SCSS HEX */
$oxford-blue: #001025ff;
$blast-off-bronze: #af7a6dff;
$mustard: #fdd643ff;
$white: #fefcfdff;
$cadet: #5d737eff;

/* SCSS HSL */
$oxford-blue: hsla(214, 100%, 7%, 1);
$blast-off-bronze: hsla(12, 29%, 56%, 1);
$mustard: hsla(47, 98%, 63%, 1);
$white: hsla(330, 50%, 99%, 1);
$cadet: hsla(200, 15%, 43%, 1);

/* SCSS RGB */
$oxford-blue: rgba(0, 16, 37, 1);
$blast-off-bronze: rgba(175, 122, 109, 1);
$mustard: rgba(253, 214, 67, 1);
$white: rgba(254, 252, 253, 1);
$cadet: rgba(93, 115, 126, 1);

$oxford-blue-gradient: linear-gradient(
  160deg,
  rgba(0, 16, 37, 1) 85%,
  rgba(93, 115, 126, 1) 100%
);

/* SCSS Gradient */
$gradient-top: linear-gradient(
  0deg,
  #001025ff,
  #af7a6dff,
  #fdd643ff,
  #fefcfdff,
  #5d737eff
);
$gradient-right: linear-gradient(
  90deg,
  #001025ff,
  #af7a6dff,
  #fdd643ff,
  #fefcfdff,
  #5d737eff
);
$gradient-bottom: linear-gradient(
  180deg,
  #001025ff,
  #af7a6dff,
  #fdd643ff,
  #fefcfdff,
  #5d737eff
);
$gradient-left: linear-gradient(
