  #fefcfdff,
  #5d737eff
);
$gradient-top-right: linear-gradient(
  45deg,
  #001025ff,
  #af7a6dff,
  #fdd643ff,
  #fefcfdff,
  #5d737eff
);
$gradient-bottom-right: linear-gradient(
  135deg,
  #001025ff,
  #af7a6dff,
  #fdd643ff,
  #fefcfdff,
  #5d737eff
